.main_container{
    width: 100%;
    height: 36rem;
    display: flex;
    align-items: center;
}
.left_box{
    display: flex;
    width: 26.75%;
}
.line{
    background-color: white;
    height: 90%;
    width: 5px;
    border-radius: 100px;
}
.small_line{
    background-color: white;
    height: 60%;
    width: 5px;
    border-radius: 100px;
}


.first_line{
    margin-left: 100px;
}

.mid_box{
    display: flex;
    width: 32%;
    height: 100%;
    align-items: center;
    flex-direction: column;
    justify-content: center;
}

.right_box{
    margin-left: 20px;
    display: flex;
    width: 30%;
    height: 100%;
    justify-content: center;
    align-items: center;
    flex-direction: column
}



.main_name{
    color: white;
    font-size: 120px;
}

.main_fullname{
    color: rgba(255, 255, 255, 0.575);
    font-size: 20px;
    margin-bottom: 30px;
}

.application{
    font-size: 20px;
    background-color: white;
    text-decoration: none;
    color: rgba(14, 14, 14, 0.76);
    border-radius:25px;
    padding: 8px 20px 8px 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 30px;
}

.guide_box{
    margin-top: 120px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.angleup_icon{
    color: white;
    font-size: 50px;
    margin-bottom: 5px;
}
.guide_text{
    color: white;
    font-size: 20px;
    text-decoration: none;
    display: flex;
    text-align: center;
    width: 250px;
}

.lock_icon{
    font-size: 250px;
    color: white;
    margin-top: 150px;
    z-index: 100;
}
.lockopen_icon{
    font-size: 250px;
    color: white;
    margin-top: 150px;
    display: none;
    margin-left: 60px;
    z-index: 100;
}

.right_box:hover .lock_icon_back{
    display: none;
}

.right_box:hover .lockopen_icon{
    display: block;
}

@keyframes circle-size {
    to {
      height: 40px;
    }
}


@media (max-width:855px){
    .line{
        display: none;
    }
    .main_container{
        display: flex;
        flex-direction: column;
    }
    .left_box{
        display: none;
    }
    .right_box{
        margin-left: 0px;
    }
    .guide_box{
        margin-bottom: 80px;
    }
    .main_fullname{
        width: 150px;
        display: flex;
        text-align: center;
    }
    .application{
        width: 160px;
    }
}



